.App {
  text-align: center;

  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  &.rec {
    .App-header {
      background-color: mix(#282c34, red, 80%);
    }
  }
}

.App-logo {
  animation: App-logo-spin infinite 10s linear;
  height: 40vmin;
  pointer-events: none;
}

$color: #282c34;

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }

}


#button_holder {
  display: flex;
  justify-content: space-between;
}


.buttons {
  background-color: #191d25;
  min-width: 25vh;
  min-height: 6vh;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: center;
  color: white;
  border: none;


